function getKey(keys, obj, defaultVal){
  let toReturn = null;
  for (const key of keys){
    if (obj.hasOwnProperty(key)){
      // console.log("found: ",key);
      toReturn = obj[key];
      break;
    }
  }
  if (toReturn==null){return defaultVal;}
  return toReturn;
}

//floatBytes, quant
function computeModelSize(parsedConfig){

  
  const vocab=parsedConfig['vocab'], heads=parsedConfig['heads'], numLayers = parsedConfig['num_layers'], hiddenDim = parsedConfig['hiddenDim'], interDim = parsedConfig['interDim'];

  // console.log(vocab, heads, numLayers, hiddenDim, interDim);
  // let fB = floatBytes;
  // if (quant === 'bnb_int8'){fB = 1;}
  // if (quant === 'bnb_q4'){fB = 0.5;}
  
  const out =  vocab*hiddenDim*2 + numLayers*4*hiddenDim*hiddenDim + numLayers*3*interDim*hiddenDim;
  // console.log("this is out: ", out)
  
  return out;
}

function getGradOptMemory(dropdownFullOrNot, dropdownOpt, dropdownQuant, modelSize, floatBytes, parsedConfig){

  const full=dropdownFullOrNot,opt=dropdownOpt,quant=dropdownQuant;
  console.log(full, opt, quant);


  if (full==='full_trn' && opt==='adam_opt' && quant==='no_quant'){
    return modelSize*3*floatBytes;
  }

  if (full==='full_trn' && opt==='adam_opt' && quant==='bnb_int8'){

    return modelSize*3*1 + getExtraMemory(parsedConfig, quant);; //Some extra mmeory that bnb int8 takes
  }

  if (full==='full_trn' && opt==='adam_opt' && quant==='bnb_q4'){
    //Need to check if q4 also takes extra memory
    return modelSize*3*0.5 + getExtraMemory(parsedConfig, quant);
  }
  //------------
  if (full==='full_trn' && opt==='sgd_opt' && quant==='no_quant'){
    return modelSize*1*floatBytes;
  }

  if (full==='full_trn' && opt==='sgd_opt' && quant==='bnb_int8'){
    return modelSize*1*1 + getExtraMemory(parsedConfig, quant);
  }

  if (full==='full_trn' && opt==='sgd_opt' && quant==='bnb_q4'){
    return modelSize*1*0.5 + getExtraMemory(parsedConfig, quant);
  }

  //4*layer*8*hid*4*2

  //------------
  if (full==='lora_trn' && opt==='adam_opt' && quant==='no_quant'){
    return parsedConfig.num_layers*8*parsedConfig.hiddenDim*2*4*3*2;
  }


  if (full==='lora_trn' && opt==='adam_opt' && quant==='bnb_int8'){
    console.log("here!")
    return parsedConfig.num_layers*8*parsedConfig.hiddenDim*2*4*3 + getExtraMemory(parsedConfig, quant);
  }

  if (full==='lora_trn' && opt==='adam_opt' && quant==='bnb_q4'){
    return parsedConfig.num_layers*8*parsedConfig.hiddenDim*2*4*3 + getExtraMemory(parsedConfig, quant);
  }
  //------------
  if (full==='lora_trn' && opt==='sgd_opt' && quant==='no_quant'){
    return parsedConfig.num_layers*8*parsedConfig.hiddenDim*2*4*2;
  }

  if (full==='lora_trn' && opt==='sgd_opt' && quant==='bnb_int8'){
    return parsedConfig.num_layers*8*parsedConfig.hiddenDim*2*4*1 + getExtraMemory(parsedConfig, quant);
  }

  if (full==='lora_trn' && opt==='sgd_opt' && quant==='bnb_q4'){
    return parsedConfig.num_layers*8*parsedConfig.hiddenDim*2*4*1 + getExtraMemory(parsedConfig, quant);
  }

  console.log(full, opt, quant);
  throw new Error("Invalid combination of values");
  
}

function getExtraMemory(parsedConfig, quant){

  const constant_8_overhead = 200.0, constant_8_extra=350.0;
  const constant_4_overhead = 350.0, constant_4_extra=550.0;

  const common = (10*parsedConfig.hiddenDim + 5*parsedConfig.hiddenDim + 4*parsedConfig.interDim + 2*parsedConfig.interDim)*parsedConfig.num_layers;

  let extra_mem = 0;

  if (quant === 'bnb_int8'){
    extra_mem = constant_8_overhead*common + constant_8_extra*common;
  }

  if (quant === 'bnb_q4'){
    extra_mem = constant_4_overhead*common + constant_4_extra*common;
  }

  console.log("extra mem", extra_mem);
  return extra_mem;

}

function getActivationMemory(parsedConfig, contextLen, floatBytes, quant, dropdownFullOrNot){

  const heads=parsedConfig['heads'], numLayers = parsedConfig['num_layers'], hiddenDim = parsedConfig['hiddenDim'], interDim = parsedConfig['interDim'];

  let fB = floatBytes;
  const len = contextLen;

  // if (quant==='bnb_int8'){fB=1;}
  // if (quant==='bnb_q4'){fB=0.5;}
  
  console.log("activation: ",heads, numLayers, hiddenDim, interDim);

  //const attn_per_layer = qkv + qk (transpose) + attn mat + attn mat convert tp fp32 + attn  mat divided by sqrt +  
  const attn_per_layer = (len*hiddenDim*3)*fB + (len*hiddenDim*2)*fB  + (len*len*heads)*fB   +(len*len*heads*4) + (len*len*heads)*fB + len*hiddenDim*fB + len*hiddenDim*fB + len*hiddenDim*fB
  
  // heads*len*len*4 + heads*len*len*fB + 3*hiddenDim*len*fB + hiddenDim*len*fB + hiddenDim*len*fB

  const ffn_per_layer = hiddenDim*len*fB + hiddenDim*len*fB + fB*5*len*interDim + interDim*len*fB;

  const norm = len*4*2 + len*hiddenDim*fB*6;
  
  let lora = 0;
  // if (dropdownFullOrNot==='lora_trn'){
  //   lora = (8*len*2 + hiddenDim*len*2)*4;
  // }

  const total_per_layer = attn_per_layer + ffn_per_layer + norm + lora;
  console.log("total per layer: ", convertToMB(attn_per_layer), convertToMB(ffn_per_layer), convertToMB(norm), convertToMB(lora));

  //total per layer: 4.2724609375 5.55419921875 6.409454345703125 8.02001953125
  const total = total_per_layer*numLayers;

  console.log("this is total: ", total, (attn_per_layer + ffn_per_layer));

  return total;

}

function getParseConfig(jsonData){
  
  // console.log("---parsing config ----");
  // console.log(jsonData);
  
  const vocab = getKey(["vocab_size"],jsonData,32000);
  const hiddenDim =  getKey(['hidden_size','d_model','n_embd'],jsonData, 64);
  const heads = getKey(['num_attention_heads','num_heads','n_head'], jsonData, 12);
  const interDim = getKey(['intermediate_size','n_inner','d_ff'], jsonData, 12);
  const num_layers = getKey(['num_layers', 'num_hidden_layers', 'n_layer'], jsonData, 12);

  return {
    "vocab" : vocab,
    "hiddenDim" : hiddenDim,
    "heads" : heads,
    "interDim" : interDim,
    "num_layers" : num_layers
  }
}

function getDefault(modelSize){

  //If only model size is provided. Guess the values

  let vocab = 32000;
  let heads = 32;
  let numLayers = 32;

  //vocab*h + numLayers*4*h*h + 3*4*h*h*numLayers = modelSize*10^9
  const A=numLayers*4 + 3*4*numLayers;
  const B=vocab;
  const C= -1*modelSize*billion;

  let h = (-B + Math.sqrt(B*B - 4*A*C))/(2*A);
  h = Math.ceil(h);
  
  return {
      "vocab" : vocab,
      "hiddenDim" : h,
      "heads" : heads,
      "interDim" : 4*h,
      "num_layers" : numLayers
  };

}

function convertToMB(value){
  return (value)/(1024*1024);
}

function convertToMBModelSize(value, quant){
  let extra = 0;
  let fB = 2;
  let size = (value)*fB/(1024*1024);
  if (quant==='bnb_int8' || quant==='bnb_q4'){extra = 0.06*size;}

  if (quant==='bnb_int8'){
    size = size/2;
  }
  if (quant==='bnb_q4'){
    size = size/4;
  }
  return size+extra;
}

function convertToBytes(floatType){
  return 2.0;
}

function getAllComputedData(jsonData, modelSize, contextLen, floatType, selections, setErrorMessage, openModal){

    let parsedConfig=null, modelSizeinB = null;
    let activationMemory=0, gradAndOptMemory=0;
    let inferenceMemory=0;
    let totalMemory=0;
    const floatBytes = convertToBytes(floatType);
    let overHead = 650;

    

    if (jsonData == null){

      if (!isNumberOrFloat(modelSize)){
        console.log("error with model size");
        setErrorMessage("Hugginface model id not available, enter model size as a number");
        openModal();
        return null;
      }

      parsedConfig = getDefault(modelSize);
      modelSizeinB = modelSize*billion;
    }
    else{

      parsedConfig = getParseConfig(jsonData); 
      console.log(parsedConfig);
      modelSizeinB = computeModelSize(parsedConfig);
      
    }

    let fB = floatBytes;
    if (selections.dropdownQuant === 'bnb_int8'){fB = 1;}
    if (selections.dropdownQuant === 'bnb_q4'){fB = 0.5;}
    const modelSizeinMB = convertToMBModelSize(modelSizeinB, selections.dropdownQuant);
    // console.log(modelSizeinB);

    //!Inference
    if (selections.dropdownTrnOrNot==='inf'){
      let fB = 2;
      if (selections.dropdownQuant==='bnb_int8'){fB=1;}
      if (selections.dropdownQuant==='bnb_q4'){fB=0.5;}

      inferenceMemory = convertToMB(2*contextLen*2*fB*parsedConfig['hiddenDim']*parsedConfig['num_layers'] * floatBytes);
      totalMemory = inferenceMemory + modelSizeinMB + overHead;
    }
    else{
      //! Train
      activationMemory = getActivationMemory(parsedConfig, contextLen, floatBytes, selections.dropdownQuant, selections.dropdownFullOrNot);
      
      activationMemory = convertToMB(activationMemory);
      console.log("got activation",activationMemory);
      
      gradAndOptMemory = getGradOptMemory(selections.dropdownFullOrNot, selections.dropdownOpt, selections.dropdownQuant, modelSizeinB, floatBytes, parsedConfig);
      
      console.log("got gradOpt",gradAndOptMemory);

      gradAndOptMemory = convertToMB(gradAndOptMemory);
      totalMemory = modelSizeinMB + gradAndOptMemory + activationMemory;
      
      console.log("got total",totalMemory);

      totalMemory = totalMemory + overHead;

    }

    return {
      'Total' : Math.ceil(totalMemory),
      'Inference Time' : Math.ceil(inferenceMemory),
      'Model Size' : Math.ceil(modelSizeinMB),
      'Activation Memory' : Math.ceil(activationMemory),
      'Grad & Optimizer memory' : Math.ceil(gradAndOptMemory),
      'cuda overhead' : overHead
    }

}




